span {
  box-shadow: 12px 12px 44px 0 rgba(0, 0, 0, 0.5),
    -12px -12px 44px 0 rgba(255, 255, 255, 0.932);
}

.fragmentOne {
  box-shadow: inset 12px 12px 20px 0 rgba(214, 214, 214, 0.5),
    inset -12px -12px 20px 0 rgb(255, 255, 255);
  background-color: rgba(255, 255, 255, 0.466);
  font-family: "Poppins", sans-serif;
  font-style: normal;
  color: rgb(85, 80, 80);
  position: absolute;
  max-height: 100%;
  line-height: 150%;
  border: none;
  font-size: 0.7vw;
  text-align: justify;
  padding: 2%;
  width: 16%;

  border: none;

  top: 34%;
  margin-left: 10%;
  cursor: pointer;
  border-radius: 25px;
}

