.leftdown {
  /*large light blue rectangle*/
  max-height: 100%;

  background: rgb(34, 193, 195);
  background: linear-gradient(
    0deg,
    rgb(106, 167, 182) 0%,
    rgba(23, 22, 46, 1) 100%
  );
  height: 75%;
  width: 20%;
  color: rgb(211, 0, 0);
  border: none;
  padding-left: 2%;
  padding-top: 2%;
  padding-bottom: -10%;

  bottom: 10%;
  top: 18%;
  position: absolute;
  margin-left: 4%;
  margin-bottom: 10px;
  border-radius: 0 0 2vw 2vw;
}
.leftup {
  /*small white rectangle*/
  max-height: 100%;
  background-color: #ffffff;
  height: 7%;
  width: 20%;
  border: none;

  padding-left: 2%;
  padding-top: 4%;
  font-size: 1.5vw;
  color: rgb(104, 17, 17);
  font-family: "Poppins", sans-serif;

  bottom: 10%;
  top: 3%;
  position: absolute;
  margin-left: 4%;
  padding-bottom: -10%;
  border-radius: 2vw 2vw 0 0;
}
