button {
  font-family: "Poppins", sans-serif;
  font-style: oblique;
  color: rgb(85, 80, 80);
  box-shadow: 12px 12px 44px 0 rgba(0, 0, 0, 0.5),
    -12px -12px 44px 0 rgba(255, 255, 255, 0.932);
  height: 15%;
  transition: all 0.2s;
  flex-direction: row;
  border: none;
}

button::-moz-focus-inner {
  border: 0;
}

.buttonOne {
  padding: 20px 20px 20px 5px;
  background-color: rgba(219, 171, 68, 0.562);

  max-height: 100%;

  width: 20%;
  border: none;
  font-size: 1vw;
  text-align: left;
  padding-left: 2%;
  border: none;
  bottom: 10%;
  top: 18%;
  position: absolute;
  margin-left: 10%;
  padding-bottom: -10%;
  cursor: pointer;
  border-radius: 10vw;
  /* font-family: "Montserrat Alternates", sans-serif; */
}
