/* section {
  position: relative;
  width: 100%;
  height: 100%;
} */
.background {
  width: 100%;
}
.buttons {
  margin-top: -100%;
  margin-bottom: 5%;

  width: 2.5%;
  height: auto;
  padding-left: 50%;
}

.buttons:active {
  transform: translateY(3px);
}

a::-moz-focus-inner {
  border: 0;
}

.section01 {
  line-height: 0;
}
.section02 {
  display: block;
  line-height: 0%;
  margin-bottom: -10px;
  position: relative;
  display: grid;
}
.section03 {
  margin-top: -10px;
  display: block;
  line-height: 0;
  margin-bottom: -10%;
  position: relative;
}
.text {
  position: absolute;
  margin-left: 27%;
  margin-top: 4%;
  font-size: 2vw;
  text-align: center;
  color: rgb(43, 40, 40);
  font-family: "Poppins", sans-serif;
}
